body {
  height: 100vh;
  background-color: #040406;
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #2F769D;
    height: 13vh;
}

.myname-title{
    float: left;
    font-family: 'Press Start 2P', cursive; 
    font-size: 6vh;
    margin: 0;
    padding-left: 3vw;
    position: relative;
    bottom: -6.25vh;
    color: #17FB3F;
}
.navigator{
    width: 100vw;
}

.nav-links{
    align-content: center;
    list-style: none;
    position: fixed;
    padding: 0.1em 0.35em 0.1em 0.1em;
    left: 3vw;
    top: 20vh;
    border: 5px inset #FF9517;
    border-radius: 15px;
}

.nav-button{
    width: 100%;
    font-family: 'Press Start 2P', cursive;
