.projects-grid{
    margin-left: 33%;
    display: grid;
    grid-template-columns: 20vw 20vw 20vw;
    grid-template-rows: 20vh 20vh 20vh;
    grid-gap: 3% 3%;
    grid-auto-flow: row;
}

.project-icon{
    border: 1px dashed #32BD4A;
    border-radius: 5px;
}

